// 路由基本配置
// 路由传参(以及获取参数-props)
// 动态路由(/:page)
// js跳转
// 权限路由
// npm i antd-mobile --registry https://
const RouteWithSubRoutes = (route) => {
	if(route.pri) return <PrivateRoute {...route}/>
	else{
		return <Route path={route.path} 
		render={props => (	
			// pass the sub-routes down to keep nesting
			<route.component {...props} routes={route.routes}/>
			)}
			/>
		}
	}

const PrivateRoute = ({ component:Com, ...rest }) =>(
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? (<Com routes={{...rest}.routes}/>)
					: (
						<Redirect
							to={ {
								pathname: "/login",
								state: location
							} }
						/>
					)
			}
		/>
	)

const App = () => {
	return <Router>
		<Header/>
		<Switch>
			{
				routes.map((route,i)=>{
					return <RouteWithSubRoutes key={i} {...route} />
				})
			}
		</Switch>
	</Router>
}

export default App;
export {RouteWithSubRoutes};

